
import Tab from 'vux/src/components/tab/tab.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import Group from 'vux/src/components/group/index.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import reportService from '../service/reportService'
import saleReportService from '../service/saleRepotService'
import Loading from './loading'
import BScroll from 'better-scroll'

const PROJ_LIST = 'ROSE_PROJ_LIST';
const PAGE_SIZE = 30;
export default {
components: {
Tab,
Cell,
Group,
TabItem,
Calendar,
CellFormPreview,
Grid,
GridItem,
PopupPicker,
Selector,
Divider,
Loading
},
data() {
return {
showTab: true, // 
activeIndex: 0,
filterDate: [], // 
reportList: [], // 
curTab: 'days',
dateList: [
{
name: '',
value: 'yesterdays'
},
{
name: '',
value: 'days'
},
{
name: '',
value: 'weeks'
},
{
name: '',
value: 'months'
},
{
name: '',
value: 'years'
},
], // 
reportData: {}, // 
filterParams: {}, // 
showDate: false, // 
dateSelected: { // 
name: '',
value: 'days'
},
showProj: false, // 
projList: [], // 
page: 1, // 
curPage: 1, // 
objName: '', // 
isDisabled: false,
showLoading: false,
totalData: {}, // 
totalText: '', // 
rankScroll: null
}
},
methods: {
onItemClick(item) {
this.curTab = item.value;
this.reportList = this.reportData[item.value]
},
onChange(val) {
console.log(val);

},

resetData(key = 'reportData') {
this[key] = {
yesterdays: [],
days: [],
weeks: [],
months: [],
years: []
}
},

assembleData(params = {}) {
return new Promise((resolve, reject) => {
this.showLoading = true;
reportService.getReport(Object.assign(this.filterParams, {
objName: this.objName,
pageNo: this.page
})).then(repData => {
this.showLoading = false;
this.resetData();
let map = ['yesterdays', 'days', 'weeks', 'months', 'years'];

map.forEach(item => {
repData[item] && repData[item].forEach((data, index) => {
let detail = [
{
label: '',
value: `${data.qty || 0}/`
}, {
label: '',
value: `${numberComma(data.amount || 0)}`
}, {
label: 'A',
value: `${numberComma(data.aProduct || 0)}`
}, /*{
label: 'B',
value: '999'
},*/ {
label: '',
value: data.sybName || ''
}, {
label: '',
value: data.bankName || ''
}, {
label: '',
value: data.shengName || ''
}, {
label: '',
value: data.bmName || ''
},
];
if (this.objName === '') { // A
detail.shift();
detail.shift();
}
this.reportData[item].push({
name: `${(index + 1) + (this.page - 1) * PAGE_SIZE}. ${data.creator}`,
sales: this.objName ? `${data.qty || 0}/` : `${numberComma(data.aProduct || 0)}`,
showContent: false,
detail: detail
})
})
});
this.curPage = this.page;
this.reportList = this.reportData[this.dateSelected.value];
this.isDisabled = this.reportList.length < PAGE_SIZE;
this.$nextTick(() => {
if (!this.rankScroll) {
this.rankScroll = new BScroll(this.$refs.rankContainer, {click: true})
}
this.rankScroll.refresh();
resolve();
});
}).catch(err => {
this.showLoading = false;
this.resetData();
this.reportList = [];
this.$vux.alert.show({
content: err.message
})
})
})
},

getProj() {
let proj = JSON.parse(sessionStorage.getItem(PROJ_LIST));
proj.shift();
this.projList = proj
},

hideDropList() {
this.showDate = false;
this.showProj = false;
},

dateClick() {
this.showDate = !this.showDate;
this.showProj = false;
},

dateItemClick(item) {
this.showDate = false;
if (item.value === this.dateSelected.value) {
console.log('');
return
}
this.dateSelected = item;
this.reportList = this.reportList.map(item => {
return Object.assign(item, {
showContent: false
})
});
this.getTotalText();
if (this.page === 1) { // 
this.reportList = this.reportData[item.value];
this.isDisabled = this.reportList.length < PAGE_SIZE
} else {
this.page = 1;
this.assembleData()
}
},

projClick() {
this.showProj = !this.showProj;
this.showDate = false;
},

projItemClick(item, index) {
this.showProj = false;
if (item === this.objName) {
console.log('');
return
}
this.objName = item;
this.page = 1;
this.assembleData();
this.getTotal();
},

aProjClick() {
this.showProj = false;
this.showDate = false;
this.objName = '';
this.page = 1;
this.assembleData();
this.getTotal();
},

pagePrev() {
if (this.page === 1) {
return
}
this.page--;
this.assembleData().then(() => {

this.rankScroll.scrollTo(0, 0);
});
},

pageNext() {
if (this.isDisabled) {
return
}
this.page++;
this.assembleData().then(() => {

this.rankScroll.scrollTo(0, 0);
});
},

getTotal() {
reportService.getTotal(Object.assign(this.filterParams, {
objName: this.objName
})).then(data => {
this.totalData = data;
this.getTotalText();
})
},

getTotalText() {
let totalData = this.totalData[this.dateSelected.value] || [];
let total = {};
totalData.every(item => {
if (item.objName && item.objName === this.objName) {
total = item;
return false
}
if (item.objName === 'A' && !this.objName) {
total = item;
return false
}
return true
});
if (!this.objName) { // A
this.totalText = `${numberComma(total.amount || 0)}${total.number || 0}`;
} else {
this.totalText = `${total.qty || 0}/${total.number || 0}`;
}
},
},
filters: {

nameFilter(val) {
return val.length > 5 ? `${val}...` : val || ''
}
},
created() {
let query = this.$route.query;
this.resetData();
this.resetData('totalData');

this.filterParams = {
shengName: query.region ? decodeURI(query.region) : '', // 
bankName: query.bank ? decodeURI(query.bank) : '', // 
bmName: query.dept ? decodeURI(query.dept) : '', // 
objName: query.proj ? decodeURI(query.proj) : '', // 
};

if (this.filterParams.objName) {
this.objName = this.filterParams.objName;
}
this.getProj();
this.assembleData();
this.getTotal();
},
mounted() {
this.$nextTick(() => {
if (!this.rankScroll) {
this.rankScroll = new BScroll(this.$refs.rankContainer, {click: true})
}
})
}
}



